html {
  background-color: rgb(102,166,176);
}

.Clima {
  margin: 3.75rem 2.5rem;
  display: grid;
  grid-template-columns: 1fr 3fr;
  gap: 5px;
  background-color: rgb(14, 125, 125);
}

.Clima > * {
  border-radius: 15px;
  border: solid 1px orange;
}

.temp-actual {
  height: 40vh;
  width: 27vw;
}

.max-min {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 15rem;
}

.container-extra {
  width: 68.4vw;
}

.por-hora {
  height: 29vh;
  width: 68.2vw;
  box-sizing: border-box;
}

.highlight-header {
  margin-bottom: 1vh;
}

.container-highlights {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr;
  row-gap: 10px;
  column-gap: 8px;
}
